export const pinyinUtil = {
	//
	parseDict() {
		dict.firstletter = pinyin_dict_firstletter
		dict.notone = {}
		dict.py2hz = pinyin_dict_notone
		for (var i in pinyin_dict_notone) {
			var temp = pinyin_dict_notone[i]
			for (var j = 0, len = temp.length; j < len; j++) {
				dict.notone[temp[j]] = i
			}
		}
	},
	//
	getPinyin(chinese, splitter, withtone, polyphone) {
		if (!chinese || /^ +$/g.test(chinese)) return '';
		splitter = splitter == undefined ? '' : splitter;
		withtone = withtone == undefined ? false : withtone;
		polyphone = polyphone == undefined ? false : polyphone;
		var result = [];
		if (dict.withtone) { // 
			for (var i = 0, len = chinese.length; i < len; i++) {
				var pinyin = dict.withtone[chinese[i]];
				if (pinyin) {
					if (!polyphone) pinyin = pinyin.replace(/ .*$/g, ''); // 
					if (!withtone) pinyin = this.removeTone(pinyin); // 
				}
				result.push(pinyin || chinese[i]);
			}
		} else if (dict.notone) { // 
			if (withtone) console.warn('pinyin_dict_notone Dictionary files do not support tones!');
			if (polyphone) console.warn('pinyin_dict_notone Dictionary files do not support polyphony!');
			for (var i = 0, len = chinese.length; i < len; i++) {
				var temp = chinese.charAt(i);
				if (dict.notone[temp])
					dict.notone[temp] = dict.notone[temp].substring(0, 1).toUpperCase() + dict.notone[temp]
					.substring(1);
				result.push(dict.notone[temp] || temp);
			}
		} else {
			throw 'Sorry, no suitable dictionary files were found!';
		}
		if (!polyphone) return result.join(splitter);
		else {
			if (window.pinyin_dict_polyphone) return parsePolyphone(chinese, result, splitter, withtone);
			else return handlePolyphone(result, ' ', splitter);
		}
	},
	//
	getFirstLetter(str, polyphone) {
		polyphone = polyphone == undefined ? false : polyphone;
		if (!str || /^ +$/g.test(str)) return '';
		if (dict.firstletter) // 
		{
			var result = [];
			for (var i = 0; i < str.length; i++) {
				var unicode = str.charCodeAt(i);
				var ch = str.charAt(i);
				if (unicode >= 19968 && unicode <= 40869) {
					ch = dict.firstletter.all.charAt(unicode - 19968);
					if (polyphone) ch = dict.firstletter.polyphone[unicode] || ch;
				}
				result.push(ch);
			}
			if (!polyphone) return result.join(''); // 
			else return handlePolyphone(result, '', ''); // result['D', 'ZC', 'F']
		} else {
			var py = this.getPinyin(str, ' ', false, polyphone);
			py = py instanceof Array ? py : [py];
			var result = [];
			for (var i = 0; i < py.length; i++) {
				result.push(py[i].replace(/(^| )(\w)\w*/g, function(m, $1, $2) {
					return $2.toUpperCase();
				}));
			}
			if (!polyphone) return result[0];
			else return simpleUnique(result);
		}
	},
	//
	getHanzi(pinyin) {
		if (!dict.py2hz) {
			throw 'Sorry, no suitable dictionary files were found!';
		}
		return dict.py2hz[pinyin] || '';
	},
	// xio mng tng xu  xiao ming tong xue
	removeTone(pinyin) {
		var toneMap = {
			"": "a1",
			"": "a2",
			"": "a3",
			"": "a4",
			"": "o1",
			"": "o2",
			"": "o3",
			"": "o4",
			"": "e1",
			"": "e2",
			"": "e3",
			"": "e4",
			"": "i1",
			"": "i2",
			"": "i3",
			"": "i4",
			"": "u1",
			"": "u2",
			"": "u3",
			"": "u4",
			"": "v0",
			"": "v1",
			"": "v2",
			"": "v3",
			"": "v4",
			"": "n2",
			"": "n3",
			"": "m2"
		};
		return pinyin.replace(/[]/g, function(m) {
			return toneMap[m][0];
		});
	}
};

// ['D', 'ZC', 'F'] ['DZF', 'DCF']
function handlePolyphone(array, splitter, joinChar) {
	splitter = splitter || '';
	var result = [''],
		temp = [];
	for (var i = 0; i < array.length; i++) {
		temp = [];
		var t = array[i].split(splitter);
		for (var j = 0; j < t.length; j++) {
			for (var k = 0; k < result.length; k++)
				temp.push(result[k] + (result[k] ? joinChar : '') + t[j]);
		}
		result = temp;
	}
	return simpleUnique(result);
}

//
function parsePolyphone(chinese, result, splitter, withtone) {
	var poly = window.pinyin_dict_polyphone;
	var max = 7; // 710
	var temp = poly[chinese];
	if (temp) // 
	{
		temp = temp.split(' ');
		for (var i = 0; i < temp.length; i++) {
			result[i] = temp[i] || result[i];
			if (!withtone) result[i] = pinyinUtil.removeTone(result[i]);
		}
		return result.join(splitter);
	}
	for (var i = 0; i < chinese.length; i++) {
		temp = '';
		for (var j = 0; j < max && (i + j) < chinese.length; j++) {
			if (!/^[\u2E80-\u9FFF]+$/.test(chinese[i + j])) break; // 
			temp += chinese[i + j];
			var res = poly[temp];
			if (res) // 
			{
				res = res.split(' ');
				for (var k = 0; k <= j; k++) {
					if (res[k]) result[i + k] = withtone ? res[k] : pinyinUtil.removeTone(res[k]);
				}
				break
			}
		}
	}
	// 
	for (var i = 0; i < result.length; i++) {
		result[i] = result[i].replace(/ .*$/g, '')
	}
	return result.join(splitter)
}

// 
function simpleUnique(array) {
	var result = []
	var hash = {}
	for (var i = 0; i < array.length; i++) {
		var key = (typeof array[i]) + array[i];
		if (!hash[key]) {
			result.push(array[i])
			hash[key] = true
		}
	}
	return result
}

pinyinUtil.parseDict()
pinyinUtil.dict = dict
module.exports = {
	pinyinUtil: pinyinUtil
}